* {
  margin: 0;
  padding: 0;
}

.flex {
  display: flex;
}

#contact {
  flex-direction: row;
  justify-content: center;
  align-items: baseline;
  color: #1f1f1f;
  gap: 2vh;
}

.contacts-section {
  flex-direction: column;
  gap: 1vh;
}

.contact {
  flex-direction: row;
  align-items: center;
  font-weight: bold;
  color: #1f1f1f;
  height: 30px;
}

.contact img {
  height: 30px;
  width: 30px;
  margin: 0 10px;
}

@media (max-width: 500px) {
  /* mobile navbar style */
  .nav-btn {
    height: 50px;
    width: 50px;
  }
  .nav-btn p {
    display: none;
  }
  #id-home {
    background: url("../assets/navbar/icons8-home-50.png");
  }
  #id-about {
    display: none;
  }
  #id-portfolio {
    background: url("../assets/navbar/icons8-view-quilt-50.png");
  }
  #id-contact {
    background: url("../assets/navbar/icons8-contacts-50.png");
  }
  /* mobile home section style */
  #home {
    flex-direction: column-reverse;
  }
  .home-info {
    width: 380px;
    margin: auto;
  }
  #home-photo {
    height: 400px;
    width: 400px;
    background-image: url("../assets/home/me.png");
    background-size: 400px;
    animation: 15s cool-photo-border infinite alternate;
    position: relative;
    margin: 3vh auto 1vh;
    border: 5px solid rgba(255, 255, 255, 0.5);
  }
  /* mobile about section style */
  #about {
    width: 380px;
    margin: 2vh auto;
  }
  #about h1 {
    text-align: center;
  }
  #about p {
    margin: 1vh 0;
  }
  /* mobile portfolio section style */
  #portfolio {
    width: calc(100% - 4vh);
    margin: 2vh;
    display: flex;
    flex-direction: column;
    gap: 1vh;
  }
  #portfolio h1 {
    text-align: center;
  }
  .project {
    background: rgba(129, 129, 129, 0.3);
    width: calc(100% - 24px);
    margin: 0 auto;
    padding: 12px;
    border-radius: 18px;
  }
  .project-promo {
    background: rgba(129, 129, 129, 0.3);
    border-radius: 12px;
  }
  .progect-img {
    width: 100%;
    aspect-ratio: 16/9;
    border-radius: 12px;
    border: none;
  }
  .project-links {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
  .project-github-link {
    height: 30px;
    width: 30px;
    background: url("https://cdn.jsdelivr.net/gh/devicons/devicon@latest/icons/github/github-original.svg");
  }
  .project-website-link {
    font-weight: bold;
    letter-spacing: 2px;
  }
  .project-descrip,
  .project-descrip p {
    margin: 1vh 0;
  }
  .project-stack {
    text-align: center;
    letter-spacing: 2px;
  }
  /* mobile portfolio section style */
  #contact {
    width: 100%;
    text-align: center;
  }
  .footer-contacts-section {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .contact {
    display: flex;
    align-items: center;
    font-weight: bold;
  }
  .contact img {
    height: 3vh;
    margin-right: 2vh;
  }
}
/* animations */
@keyframes cool-photo-border {
  0%, 100% {
    border-radius: 50% 50% 60% 70%;
  }
  15% {
    border-radius: 60% 70% 60% 60%;
  }
  30% {
    border-radius: 50% 50% 80% 60%;
  }
  45% {
    border-radius: 60% 70% 60% 60%;
  }
  60% {
    border-radius: 60% 70% 50% 50%;
  }
  75% {
    border-radius: 70% 60% 70% 50%;
  }
  90% {
    border-radius: 60% 70% 50% 50%;
  }
}

